.sfgov-icon-location {
  &::before {
    content: '';
    display: block;
    width: 29px;
    height: 29px;
    background: url("../images/icons/location.png");
    background: url("../images/icons/location.svg") no-repeat;
  }
}
.sfgov-icon-phone {
  &::before {
    content: '';
    display: block;
    width: 29px;
    height: 29px;
    background: url("../images/icons/phone.png");
    background: url("../images/icons/phone.svg") no-repeat;
  }
}
.sfgov-icon-mail {
  &::before {
    content: '';
    display: block;
    width: 29px;
    height: 29px;
    background: url("../images/icons/mail.png");
    background: url("../images/icons/mail.svg") no-repeat;
  }
}

.sfgov-icon-department {
  &::before {
    content: '';
    display: block;
    width: 22px;
    height: 24px;
    background-image: url("../images/icons/department.png");
    background-image: url("../images/icons/department.svg");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
  }
}

.sfgov-icon-filefilled {
  &::before {
    content: '';
    display: block;
    width: 22px;
    height: 24px;
    background-image: url("../images/icons/fileFilled.png");
    background-image: url("../images/icons/fileFilled.svg");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
  }
}
