@import './public/css/core/variables';
@import './public/css/core/mixins';

.source-sans-pro {
  @include source-sans-pro;
}

.merriweather {
  @include merriweather;
}

.lead-paragraph {
  @include merriweather;
  font-size: 20px;
  line-height: 34px;
  color: $color-primary-darker;
}

a:visited {
  color: $color-primary;
}

a:hover {
  color: $color-primary-darker;
}

